*,
*::after,
*::before {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

a {
  text-decoration: none;
  color: inherit;
  display: block;
}

body {
  font-family: 'Montserrat', sans-serif;
  font-size: 1rem;
  line-height: 1.5;
  background-color: #fff;
  color: #252527;
}

ul {
  list-style-type: none;
}

img {
  display: block;
  max-width: 100%;
}